<template>
  <div class="userManage admin-layout">
    <div class="header">
      <el-form :model="form" ref="form" inline>
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="main">
      <el-card>
        <div slot="header">
          <el-button type="primary" @click="add">新增</el-button>
        </div>
        <el-table border :data="tableData" ref="tableData">
          <el-table-column type="selection" align="center" />
          <el-table-column label="用户名" prop="username" align="center" />
          <el-table-column label="手机号" prop="mobile" align="center" />
          <el-table-column label="地址" prop="address" align="center" />
          <el-table-column label="我的抽成" prop="cut" align="center" />
          <el-table-column label="操作" align="center">
            <template slot-scope="{ row }">
              <el-button type="text" @click="check(row)">查看</el-button>
              <el-button type="text" @click="edit(row)">编辑</el-button>
              <el-button type="text" @click="del(row)">删除</el-button>
              <el-button type="text" @click="recommend(row)"
                >推荐的用户</el-button
              >
              <el-button type="text" @click="bill(row)">账单</el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination
            :total="total"
            :page.sync="form.page"
            :limit.sync="form.pageSize"
            @pagination="getList"
          />
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: 'UserManage',
  data () {
    return {
      form: {
        username: '',
        mobile: '',
        address: '',
        page: 1,
        pageSize: 10
      },
      total: 0,
      tableData: [
        {
          username: '小明',
          mobile: '1587456987',
          address: '福建省厦门湖里区',
          cut: '15'
        }
      ]
    }
  },
  methods: {
    search () {
      this.form.page = 1
      this.getList()
    },
    reset () {
      this.$refs.form.resetFields()
    },
    getList () {
      console.log('getList')
    },
    add () {
      console.log('新增')
    },
    check (row) {
      console.log('查看')
    },
    edit (row) {
      console.log('编辑')
    },
    del (row) {
      console.log('删除')
    },
    recommend (row) {
      console.log('推荐')
    },
    bill (row) {
      console.log('账单')
    }
  }
}
</script>
<style lang="scss" scoped>
.userManage {
}
</style>
